//import liraries
import React, {useCallback, useEffect, useState} from 'react';
import {Dimensions, FlatList, View} from 'react-native';
import {IScreen} from '../../../../../../../constants/Size';
import WebView, {WebViewMessageEvent} from 'react-native-webview';
import {
  useAppDispatch,
  useAppSelector,
} from '../../../../../../../hooks/useRedux';
import {useFocusEffect, useNavigation} from '@react-navigation/native';
import {NavigationName, String} from '../../../../../../../constants';
import {
  ACTION_TYPES,
  IPostMessage,
  IPostMessageCollabo,
  IPostMessageEcontractTask,
  IPostMessageUser,
  IPostMessageUserTask,
} from '../../../../../review-flow/interface';
import BottomSheetCM from '../../../../../../components/BottomSheet';
import {Avatar} from '@ui-kitten/components';
import TextCM from '../../../../../../components/Text';
import {useTranslation} from 'react-i18next';
import {
  IFormData,
  IRecipientTicketImpl,
} from '../../../../../../../model/interface/service.interface';
import {useReviewServices} from '../../../../../../../hooks/apiHooks/useReviewTicketServices';
import {ticketActions} from '../../../../../../../redux/slices';
import {IInfoForm} from '../Document/interface';
import {useTicketServices} from '../../../../../../../hooks/apiHooks/useTicketServices';
import {getConfig} from '../../../../../../../services/httpClient';
import {CacheUtils} from '../../../../../../../utils';
// create a component

type Props = {
  selectTabBottom: number;
  userTaskId: string;
  recipientDetailsId: string;
  isRealoadWebview: boolean;
  notifyActionType: any;
};

const BackgroundFlow = ({
  selectTabBottom,
  userTaskId,
  recipientDetailsId,
  isRealoadWebview,
  notifyActionType,
}: Props) => {
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const dispatch = useAppDispatch();
  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const {accessToken, language} = useAppSelector(state => state.common);
  const [showBottomSheetUser, setShowBottomSheetUser] = useState(false);
  const [dataUser, setDataUser] = useState<IPostMessageUser[]>([]);
  const [screenP, setScreenP] = useState<IScreen>({
    width: Dimensions.get('screen').width,
    height: Dimensions.get('screen').height,
  });
  const [webviewKey, setWebviewKey] = useState(0);
  const [isFristLogin, setIsFirstLogin] = useState<any>(true);

  const ReviewTicketServices = useReviewServices();
  const ListServices = useTicketServices();

  // Reload the WebView when the screen is focused
  useFocusEffect(
    useCallback(() => {
      if (notifyActionType === 'email') {
        // check click from mail auto open review flow first click
        CacheUtils.getReloadWebview().then(value => {
          if (value === 'first') {
            setIsFirstLogin(true);
          } else {
            setIsFirstLogin(false);
          }
        });
      }
      setWebviewKey(prevKey => prevKey + 1); // Change key to reload WebView

      return CacheUtils.clearReloadWebview();
    }, [isRealoadWebview]),
  );

  useEffect(() => {
    Promise.all([
      getRecipientImpl(),
      getDataRecipientSLA(),
      getAllFormByTicket(),
      getModelerById(),
    ]);
  }, []);

  useEffect(() => {
    const sub = Dimensions.addEventListener('change', ({screen}) => {
      const newScreen = {
        width: screen.width,
        height: screen.height,
      };
      setScreenP(newScreen);
    });

    return () => sub.remove();
  }, []);

  const getRecipientImpl = async () => {
    try {
      const dataRecipientImpl =
        (await ReviewTicketServices.getRecipientDetaiImpl(
          dataTicketRedux.id,
          accessToken,
        )) as IRecipientTicketImpl[];
      if (dataRecipientImpl) {
        dispatch(ticketActions.setDataRecipientDetailImpl(dataRecipientImpl));
      }
    } catch (error) {}
  };

  const getDataRecipientSLA = async () => {
    try {
      const data = await ReviewTicketServices.getListTaskSLA(
        dataTicketRedux.id,
      );
      if (data) {
        dispatch(ticketActions.setDataListRecipientsSLA(data));
      }
    } catch (error) {}
  };

  const getAllFormByTicket = async () => {
    try {
      if (!dataTicketRedux.modelerId) return;
      let data = dataTicketRedux?.formData.find(
        d => !d.userTaskId,
      ) as IFormData;
      const dataResp = (await ReviewTicketServices.getFormDetailById(
        data?.eformId,
        accessToken,
      )) as IInfoForm;
      if (dataResp) {
        dispatch(ticketActions.setFormData(dataResp));
      }
    } catch (error) {}
  };

  const getModelerById = async () => {
    try {
      const data = await ListServices.getModeler(
        dataTicketRedux.modelerId,
        accessToken,
      );
      if (data) {
        dispatch(ticketActions.setModeler(data));
      }
    } catch (error) {}
  };

  const handleMessageFlow = (message: WebViewMessageEvent) => {
    const data = JSON.parse(message.nativeEvent.data) as IPostMessage<any>;

    switch (data?.action) {
      case ACTION_TYPES.SHOW_USERTASK_INFO:
        CacheUtils.saveReloadWebview();
        navigation.navigate(NavigationName.ReviewFlow, {
          // dataTicket: dataTicket,
          dataUserTask: data.data as IPostMessageUserTask,
        });
        return;
      case ACTION_TYPES.SHOW_ECONTRACT_INFO:
        navigation.navigate(NavigationName.ReviewEcontractFlow, {
          dataUserTask: data.data as IPostMessageEcontractTask,
          isFinish: true,
        });
        return;
      case ACTION_TYPES.SHOW_EXECUTOR_USER:
        setDataUser(data.data);
        setShowBottomSheetUser(true);
        return;
      case ACTION_TYPES.SHOW_EID_INFO:
        navigation.navigate(NavigationName.ReviewEIDScreen, {
          dataUserTask: data.data,
        });
        return;
      case ACTION_TYPES.CREATE_ECONTRACT_ENV:
        navigation.navigate(NavigationName.ReviewEcontractFlow, {
          dataUserTask: data.data as IPostMessageEcontractTask,
          isFinish: false,
        });
        return;
      case ACTION_TYPES.SHOW_COLLABO_INFO:
        navigation.navigate(NavigationName.ReviewEcollaboFlow, {
          dataUserTask: data.data as IPostMessageCollabo,
        });
        return;
      default:
        return;
    }
  };

  const getUrlView = () => {
    if (selectTabBottom === 1) {
      return `${dataTicketRedux?.id}?_typeView=mine&access_token=${accessToken}`;
    } else {
      if (notifyActionType === 'email' && isFristLogin) {
        return `${dataTicketRedux?.id}?_recipientDetailsId=${recipientDetailsId}&_userTaskId=${userTaskId}&_typeView=required&access_token=${accessToken}&from=${notifyActionType}`;
      }
      return `${dataTicketRedux?.id}?_recipientDetailsId=${recipientDetailsId}&_userTaskId=${userTaskId}&_typeView=required&access_token=${accessToken}`;
    }
  };

  const renderUser = () => {
    return (
      <FlatList
        data={dataUser}
        keyExtractor={item => item.id}
        renderItem={({item}) => (
          <View
            style={{
              flexDirection: 'row',
              columnGap: 10,
              alignItems: 'center',
              marginBottom: 20,
            }}>
            <Avatar
              source={{
                uri:
                  item.avatar ||
                  'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
              }}
            />
            <View style={{flex: 1}}>
              <TextCM style={{flex: 1}}>{'' + item.personalName}</TextCM>
              <TextCM>{item?.recipient?.contact?.email || '---'}</TextCM>
            </View>
          </View>
        )}
      />
    );
  };

  return (
    <>
      <View style={{flex: 1}}>
        {selectTabBottom === 1 ? (
          <WebView
            key={webviewKey}
            startInLoadingState
            // injectedJavaScript={String.SCRIPT_SCALE_WEBVIEW}
            source={{
              uri: getConfig().BASE_URL_TICKET + getUrlView(),
            }}
            onMessage={handleMessageFlow}
            webviewDebuggingEnabled
          />
        ) : (
          <>
            <WebView
              key={webviewKey}
              startInLoadingState
              // injectedJavaScript={String.SCRIPT_SCALE_WEBVIEW}
              source={{
                uri: getConfig().BASE_URL_TICKET + getUrlView(),
              }}
              onMessage={handleMessageFlow}
              webviewDebuggingEnabled
            />
          </>
        )}
      </View>
      <BottomSheetCM
        title={t('TK.listUser')}
        isVisible={showBottomSheetUser}
        onClose={() => setShowBottomSheetUser(false)}
        renderContent={renderUser()}
      />
    </>
  );
};

//make this component available to the app
export default BackgroundFlow;
