import {FlatList, useWindowDimensions} from 'react-native';
import HeaderCM from '../../components/Header/HeaderCM';
import KanBan from './components/KanBan';
import {dataListStatusTicket} from './helper';
import styles from './styles';
import {Icon, TopNavigationAction} from '@ui-kitten/components';
import {useNavigation} from '@react-navigation/native';
import NavigationName from '../../../constants/NavigationName';
import {useTranslation} from 'react-i18next';
import React from 'react';
const MyTicket = () => {
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const {width} = useWindowDimensions();

  const renderContentRight = () => {
    return (
      <>
        <TopNavigationAction
          icon={<Icon fill={'#ffffff'} name="search" />}
          onPress={() => {
            navigation.navigate(NavigationName.SearchTicketScreen, {
              type: 'my-ticket',
            });
          }}
        />
      </>
    );
  };

  return (
    <>
      <HeaderCM
        title={t('TK.myTicket')}
        renderContentRight={renderContentRight}
      />
      <FlatList
        style={{
          backgroundColor: '#fff',
        }}
        data={dataListStatusTicket}
        horizontal={true}
        pagingEnabled={true}
        showsHorizontalScrollIndicator={false}
        showsVerticalScrollIndicator={false}
        snapToAlignment="start"
        snapToInterval={width - 40}
        decelerationRate="fast"
        contentContainerStyle={[
          styles.flatListContainer,
          {
            paddingRight: 40,
          },
        ]}
        renderItem={({item}) => <KanBan dataListStatus={item} />}
      />
    </>
  );
};

export default MyTicket;
