import React, {useMemo} from 'react';
import {Federated} from '@callstack/repack/client';
import {useRoute} from '@react-navigation/native';
import SplashScreen from '../Splash';

const ComponentCM = React.lazy(() =>
  Federated.importModule('eBLDTApp', `./${'ScanScreen'}`),
);

type Props = {
  onScannedImage?: (image: string) => void;
};

const ScanScreen = ({onScannedImage}: Props) => {
  const route = useRoute();

  return (
    <React.Suspense fallback={<SplashScreen app={''} />}>
      <ComponentCM onScannedImage={onScannedImage} />
    </React.Suspense>
  );
};

export default ScanScreen;
