//import liraries
import React, {useEffect, useRef, useState} from 'react';
import WebView, {WebViewMessageEvent} from 'react-native-webview';
import {useAppSelector} from '../../../../../../../hooks/useRedux';
import {SafeAreaView} from 'react-native-safe-area-context';
import {String} from '../../../../../../../constants';
import {getConfig} from '../../../../../../../services/httpClient';
import {useReviewServices} from '../../../../../../../hooks/apiHooks/useReviewTicketServices';
import {RefreshControl, ScrollView, View} from 'react-native';
import TextCM from '../../../../../../components/Text';
import {IndexPath, Spinner} from '@ui-kitten/components';
import {useTranslation} from 'react-i18next';
import {OverflowMenuCM} from '../../../../../../components/OverflowMenu';
import styles from './styles';

// create a component
const Flow = () => {
  const {t} = useTranslation();
  const webviewRef = useRef<WebView>(null);
  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const {accessToken} = useAppSelector(state => state.common);
  const [formData, setFormData] = useState<any>();
  const [listFrom, setListFrom] = useState<any[]>([]);
  const [selectedIndex, setSelectedIndex] = useState<IndexPath | undefined>();
  const [versionFromSelect, setVersionFromSelect] = useState<any>();
  const [isReloadForm, setIsReloadForm] = useState<boolean>(false);
  const [refreshing, setRefreshing] = useState<boolean>(false);
  const ReviewTicketServices = useReviewServices();

  useEffect(() => {
    getSubFromData(null);
  }, []);

  const getSubFromData = async (version: any) => {
    const body = {
      ticketId: dataTicketRedux?.id,
      userTaskId: null,
      version: version,
    };
    try {
      const data =
        await ReviewTicketServices.getFormVersionByTicketAndUserTaskId(
          body,
          accessToken,
        );
      if (data) {
        if (version === null) {
          const listForm = Array.from(
            Array(Math.ceil(data.versionUserTask)),
          ).map((item, idx) => idx + 1);
          const filterList = listForm.map((i, index) => ({
            id: i,
            key: `${t('CM.version')} ` + i,
            value: `${t('CM.version')} ` + i,
          }));
          setListFrom(filterList);
          setVersionFromSelect(filterList[filterList.length - 1]);
          setSelectedIndex(new IndexPath(filterList.length - 1));
        }
      }
      setFormData(data);
      return data;
    } catch (error) {
      console.log('error getSubFromData', error);
    }
  };

  const onMessage = (message: WebViewMessageEvent) => {
    const data = JSON.parse(message.nativeEvent.data);
    if (data?.result === 'e-from') {
      patchValue(null);
    }
  };

  const patchValue = async (version: any) => {
    try {
      setIsReloadForm(true);
      const data = await getSubFromData(version);
      if (data) {
        const dataFrom = data?.formData;

        const formData = JSON.stringify({
          type: 'postFormEdit',
          isReadonly: true,
          data: dataFrom,
        });

        const scriptJSX = `window.postMessage('${formData
          .replace(/\\/g, '\\\\')
          .replace(/'/g, "\\'")}', '*');`;
        setTimeout(() => {
          webviewRef?.current?.injectJavaScript(scriptJSX);
          setIsReloadForm(false);
        }, 800);
      }
    } catch (error) {
    } finally {
      setRefreshing(false);
    }
  };

  const renderDropdown = () => {
    return (
      <View style={styles.version}>
        <TextCM>{versionFromSelect?.value}</TextCM>
        {isReloadForm && <Spinner size={'small'} />}
      </View>
    );
  };

  const onRefresh = () => {
    setRefreshing(true);
    patchValue(versionFromSelect?.id);
  };

  return (
    <SafeAreaView style={{flex: 1}} edges={[]}>
      {listFrom.length > 0 && (
        <View style={styles.selectForm}>
          <TextCM>{t('CM.selectVersion')}</TextCM>
          <OverflowMenuCM
            selectedIndex={selectedIndex}
            listData={listFrom}
            setSelectedIndex={(item: IndexPath | undefined) => {
              if (item) {
                setSelectedIndex(item);
                setVersionFromSelect(listFrom[item?.row]);
                patchValue(listFrom[item?.row]?.id);
              }
            }}
            selectedTitle={renderDropdown()}
          />
        </View>
      )}
      <ScrollView
        refreshControl={
          <RefreshControl refreshing={refreshing} onRefresh={onRefresh} />
        }
        contentContainerStyle={{flex: 1}}
        style={{flex: 1}}>
        <WebView
          style={{flex: 1}}
          containerStyle={{flex: 1}}
          nestedScrollEnabled={true}
          ref={webviewRef}
          startInLoadingState
          onMessage={onMessage}
          javaScriptEnabled
          injectedJavaScript={String.SCRIPT_SCALE_WEBVIEW}
          allowsInlineMediaPlayback={true}
          mediaPlaybackRequiresUserAction={false}
          webviewDebuggingEnabled
          source={{
            uri: getConfig().BASE_URL_FORM + formData?.version,
          }}
        />
      </ScrollView>
    </SafeAreaView>
  );
};

//make this component available to the app
export default Flow;
