import {View} from 'react-native';
import {Color, Font} from '../../../../../constants';
import HeaderCM from '../../../../components/Header/HeaderCM';
import {Icon, List, ListItem} from '@ui-kitten/components';
import TextCM from '../../../../components/Text';
import {Dispatch, useEffect, useState} from 'react';
import {ITicket} from '../../../../../model/interface/service.interface';
import ModalFullScreen from '../../../../components/ModalFullScreen';
import AddTicketRelativeConfig from '../AddTicketRelative/AddTicketRelativeWithConfig';
import {IModeler} from '../../../../../model/interface/modeler.interface';
import ButtonCM from '../../../../components/Button';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';

type Props = {
  onClose: () => void;
  modeler?: IModeler;
  setListTicketRelativeSelect: Dispatch<React.SetStateAction<ITicket[]>>;
  listTicketRelativeSelect: ITicket[];
  handleSetParamsConfig: () => void;
};

const ListServicesRelative = ({
  onClose,
  modeler,
  setListTicketRelativeSelect,
  listTicketRelativeSelect,
  handleSetParamsConfig,
}: Props) => {
  const [showModal, setShowModal] = useState<boolean>(false);
  const [modelerId, setModelerId] = useState<string>('');
  const [listModeler, setListModeler] = useState<
    {
      id: string;
      name: string;
    }[]
  >([]);

  const ListServices = useTicketServices();

  useEffect(() => {
    handleGetModelerName();
  }, []);

  const handleSelectModeler = (id: string) => {
    setModelerId(id);
    setShowModal(true);
  };

  const handleTicketSelect = (
    paramsSelectTicket: ITicket[],
    modelerId: any,
  ) => {
    const dataInList = listTicketRelativeSelect?.find(
      t => t.modelerId === modelerId,
    );
    if (!dataInList) {
      setListTicketRelativeSelect([
        ...listTicketRelativeSelect,
        ...paramsSelectTicket,
      ]);
    } else {
      const listFilter = listTicketRelativeSelect?.filter(
        t => t.modelerId !== modelerId,
      );
      setListTicketRelativeSelect([...listFilter, ...paramsSelectTicket]);
    }
  };

  const handleGetModelerName = async () => {
    const modelerIds = modeler?.inheritanceModeler?.modelerIds || [];
    const dataResp = await Promise.all(
      modelerIds.map(i => ListServices.getModeler(i)),
    );
    if (dataResp) {
      setListModeler(
        dataResp?.map(d => ({
          id: d.id,
          name: d.name,
        })),
      );
    } else {
      setListModeler(
        modeler?.inheritanceModeler?.modelerIds?.map(d => ({
          id: d,
          name: d,
        })) || [],
      );
    }
  };
  return (
    <>
      <>
        <HeaderCM
          style={{
            backgroundColor: '#fff',
          }}
          contentStyle={{
            backgroundColor: '#fff',
            borderBottomWidth: 1,
            borderBottomColor: Color.borderColor,
          }}
          titleStyle={{
            color: '#000810',
          }}
          fillIconBackLeft="#000810"
          title=""
          onPressIconLeft={onClose}
        />

        <View
          style={{
            marginHorizontal: 16,
            marginTop: 16,
            backgroundColor: '#fff',
          }}>
          <TextCM
            style={{
              fontFamily: Font.InterSemiBold600,
              fontSize: 14,
            }}>{`Để hoàn thành việc chọn giao dịch liên quan, vui lòng chọn dịch vụ trong mỗi quy trình dưới đây.\nSố lượng giao dịch đã chọn ${
            listTicketRelativeSelect?.length || 0
          }/${modeler?.inheritanceModeler?.modelerIds?.length}`}</TextCM>
        </View>

        <List
          data={listModeler}
          renderItem={({item}) => (
            <ListItem
              onPress={() => handleSelectModeler(item.id)}
              title={item.name}
              accessoryLeft={
                listTicketRelativeSelect?.find(t => t.modelerId === item.id) ? (
                  <Icon
                    name="checkmark-circle-2-outline"
                    width={24}
                    height={24}
                  />
                ) : undefined
              }
              accessoryRight={
                <Icon name="arrow-ios-forward-outline" width={24} height={24} />
              }
            />
          )}
        />

        <ButtonCM
          disabled={
            listTicketRelativeSelect?.length !==
            modeler?.inheritanceModeler?.modelerIds?.length
          }
          style={{marginHorizontal: 16}}
          label="Xác nhận"
          onPress={handleSetParamsConfig}
        />

        <ModalFullScreen
          isVisible={showModal}
          children={
            <AddTicketRelativeConfig
              configFilter={modeler?.configFilter}
              inheritanceModeler={modeler?.inheritanceModeler}
              onClose={() => setShowModal(false)}
              modelerId={modelerId}
              onGoBack={handleTicketSelect}
              paramsSelectTicket={
                listTicketRelativeSelect?.find(t => t.modelerId === modelerId)
                  ? ([
                      listTicketRelativeSelect?.find(
                        t => t.modelerId === modelerId,
                      ),
                    ] as any)
                  : []
              }
            />
          }
        />
      </>
    </>
  );
};

export default ListServicesRelative;
